"use client"

import { useState, useEffect } from "react"
import { Navigation } from "@/components/navigation"
import { TransactionItem } from "@/components/transaction-item"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { SearchIcon } from 'lucide-react'
import { toast } from "sonner"

interface Transaction {
  id: string
  amount: number
  type: "income" | "expense"
  reason: string
  category: string
  date: string
}

export default function TransactionsPage() {
  const [search, setSearch] = useState("")
  const [filter, setFilter] = useState<"all" | "daily" | "weekly" | "monthly">("all")
  const [transactions, setTransactions] = useState<Transaction[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    fetchTransactions()
  }, [])

  const fetchTransactions = async () => {
    try {
      setLoading(true)
      const response = await fetch('/api/transactions/list')
      const result = await response.json()
      
      if (!response.ok || !result.success) {
        toast.error(result.error || 'Failed to fetch transactions')
        setLoading(false)
        return
      }
      
      setTransactions(result.data || [])
      setLoading(false)
    } catch (error) {
      toast.error('An unexpected error occurred')
      setLoading(false)
    }
  }

  const handleEdit = async (id: string) => {
    // For now, we'll just show a toast
    toast.info('Edit functionality would open a modal or form')
  }

  const handleDelete = async (id: string) => {
    try {
      const response = await fetch(`/api/transactions/delete/${id}`, {
        method: 'DELETE',
      })
      
      const result = await response.json()
      
      if (!response.ok || !result.success) {
        toast.error(result.error || 'Failed to delete transaction')
        return
      }
      
      toast.success('Transaction deleted successfully')
      // Refresh the transactions list
      fetchTransactions()
    } catch (error) {
      toast.error('An unexpected error occurred')
    }
  }

  const filteredTransactions = transactions.filter((t) =>
    t.reason.toLowerCase().includes(search.toLowerCase()) ||
    t.category.toLowerCase().includes(search.toLowerCase())
  )

  if (loading) {
    return (
      <div className="flex min-h-screen items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-indigo-600 mx-auto"></div>
          <p className="mt-4 text-gray-600">Loading transactions...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="flex min-h-screen flex-col md:flex-row">
      <Navigation />
      <main className="flex-1 p-4 pb-20 md:p-8 md:pb-8">
        <div className="mx-auto max-w-4xl space-y-6">
          <h1 className="text-3xl font-bold">Transactions</h1>

          <div className="relative">
            <SearchIcon className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-muted-foreground" />
            <Input
              placeholder="Search transactions..."
              value={search}
              onChange={(e) => setSearch(e.target.value)}
              className="pl-10"
            />
          </div>

          <Tabs value={filter} onValueChange={(v) => setFilter(v as any)}>
            <TabsList className="grid w-full grid-cols-4">
              <TabsTrigger value="all">All</TabsTrigger>
              <TabsTrigger value="daily">Daily</TabsTrigger>
              <TabsTrigger value="weekly">Weekly</TabsTrigger>
              <TabsTrigger value="monthly">Monthly</TabsTrigger>
            </TabsList>
            <TabsContent value={filter} className="mt-6 space-y-4">
              {filteredTransactions.length > 0 ? (
                filteredTransactions.map((transaction) => (
                  <TransactionItem
                    key={transaction.id}
                    transaction={transaction}
                    onEdit={handleEdit}
                    onDelete={handleDelete}
                  />
                ))
              ) : (
                <div className="text-center py-12 text-muted-foreground">
                  No transactions found
                </div>
              )}
            </TabsContent>
          </Tabs>
        </div>
      </main>
    </div>
  )
}